.footSection{
    z-index: 7;
    width: 100%;
    min-height: 48px;
    box-sizing: border-box;
    display: block;
}

.topFooter{
    display: block;
    background: #ebeeef;
    border-top: 1px solid rgba(0, 47, 52, .2);
    font-size: 14px;
    line-height: 20px;
}

.downFooter{
    margin: auto;
    width: 100%;
    padding: 16px;
    max-width: 1100px;
    display: flex;
    justify-content: space-between;
}
.downFooter section{
    font-weight: 600;
    text-transform: uppercase;
}

.downFooter section ul{
    margin: 0;
    padding: 0;
    list-style: none;
    margin-top: 8px;
}

.downFooter section ul li a{
    display: block;
    font-weight: 400;
    text-transform: none;
    text-decoration: none;
    cursor: pointer;
    font-size: 12px;
    color: rgba(0, 47, 52, .64);
}

.downFooter section ul li a:hover{
    color: black;
}

.lastFooter{
    color: #fff;
    font-size: 12px;
    line-height: 18px;
    background: #002f34;
    padding: 16px 0;
}

.lastFooter2{
    flex-direction: row-reverse;
    margin: auto;
    width: 100%;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
}

.lastFooter2 section{
    color: #fff !important;
}

.lastFooter2 section a{
 color: #fff;
}


.containerfooter{
    width: 100% !important;
    background-color: rgb(246, 246, 246) !important;
    display: flex;
    justify-content: space-evenly;
}

.imagefooter{
    margin-left: 10px;
}

.div2{
    margin-top: 60px !important;
    font-weight: bold;
}
